import ThanhPhanHoSo from './TiepNhan/TiepNhanHoSo_ThanhPhanHoSoNew.vue';
export default {
  props: ['index', 'id'],
  data: function data() {
    return {
      dialog_addTHPHS: false,
      loadingAction: false,
      thongTinChiTietHoSo: {},
      btnDossierDynamics: [],
      visibleBtnNextAction: true
    };
  },
  computed: {
    loading: function loading() {
      return this.$store.getters.loading;
    }
  },
  components: {
    'thanh-phan-ho-so': ThanhPhanHoSo
  },
  created: function created() {
    var vm = this;
    vm.initData(vm.id);
    vm.getNextActions(vm.id);
  },
  watch: {},
  methods: {
    initData: function initData(data) {
      var vm = this;
      vm.$store.dispatch('getDetailDossier', data).then(function (resultDossier) {
        vm.thongTinChiTietHoSo = resultDossier;
        vm.$refs.thanhphanhoso.initData(resultDossier);
      });
    },
    goBack: function goBack() {
      window.history.back();
    },
    getNextActions: function getNextActions(dossierId) {
      var vm = this;
      var filter = {
        dossierId: dossierId
      };
      vm.$store.dispatch('pullNextactions', filter).then(function (result) {
        vm.btnDossierDynamics = result;
      });
    },
    nextAction: function nextAction(item, index) {
      console.log('Đã bổ sung');
      var vm = this;
      console.log('luu Ho So--------------------');
      vm.$store.commit('setPrintPH', false);
      var thanhphanhoso = this.$refs.thanhphanhoso.dossierTemplateItems;
      var dossierFiles = vm.$refs.thanhphanhoso.dossierFilesItems;
      var dossierTemplates = thanhphanhoso;
      var listAction = [];
      var listDossierMark = [];

      if (dossierTemplates) {
        dossierTemplates.forEach(function (val, index) {
          if (val.partType === 1) {
            val['dossierId'] = vm.thongTinChiTietHoSo.dossierId;
            listDossierMark.push(vm.$store.dispatch('postDossierMark', val));
          }
        });

        if (dossierFiles.length !== 0) {
          dossierFiles.forEach(function (value, index) {
            if (value.eForm) {
              value['dossierId'] = vm.thongTinChiTietHoSo.dossierId;
              listAction.push(vm.$store.dispatch('putAlpacaForm', value));
            }
          });
        } else {
          dossierTemplates.forEach(function (val, index) {
            if (val.partType === 1 && val.hasForm) {
              val['dossierId'] = vm.thongTinChiTietHoSo.dossierId;
              listAction.push(vm.$store.dispatch('postEform', val));
            }
          });
        }
      }

      Promise.all(listDossierMark).then(function (values) {}).catch(function (xhr) {});
      Promise.all(listAction).then(function (values) {
        console.log(values);
        var filter = {
          dossierId: vm.thongTinChiTietHoSo.dossierId,
          actionCode: item.actionCode
        };
        var currentQuery = vm.$router.history.current.query;
        vm.loadingActionProcess = true;
        vm.$store.dispatch('processDossierRouter', filter).then(function (result) {
          vm.visibleBtnNextAction = false;
          vm.$router.push({
            path: vm.$router.history.current.path,
            query: {
              recount: Math.floor(Math.random() * (100 - 1 + 1)) + 1,
              renew: Math.floor(Math.random() * (100 - 1 + 1)) + 1,
              q: currentQuery['q']
            }
          });
        });
      }).catch(function (reject) {
        console.log('reject=============', reject);
      });
    }
  }
};