import $ from 'jquery';
import toastr from 'toastr';
toastr.options = {
  'closeButton': true,
  'timeOut': '5000'
};
export default {
  props: {
    dossierId: {
      type: String,
      default: function _default() {
        return '';
      }
    },
    formCodeInput: {
      type: String,
      default: function _default() {
        return '';
      }
    },
    onlyView: {
      type: Boolean,
      default: function _default() {
        return false;
      }
    }
  },
  data: function data() {
    return {
      validAdd: true,
      fileName: '',
      totalRecord: 1,
      totalPage: 1,
      notarizationList: [],
      pagination: {
        rowsPerPage: 2
      },
      totalNotarizations: 0,
      loading: false,
      dialog_notarization: false,
      doChangeSync: {},
      headers: [{
        text: 'STT',
        align: 'center',
        sortable: false
      }, {
        text: 'Tên tài liệu chứng thực',
        align: 'center',
        sortable: false
      }, {
        text: 'Số bản',
        align: 'center',
        sortable: false
      }, {
        text: 'Số trang',
        align: 'center',
        sortable: false
      }, {
        text: 'Thành tiền',
        align: 'center',
        sortable: false
      }, {
        text: 'Thao tác',
        align: 'center',
        sortable: false
      }],
      fee: ''
    };
  },
  created: function created() {
    var vm = this;

    if (vm.onlyView) {
      vm.headers.pop();
    }

    vm.loading = true;
    var filter = {
      dossierId: vm.dossierId
    };
    vm.$store.dispatch('getNotarization', filter).then(function (result) {
      vm.loading = false;
      vm.notarizationList = result;

      if (vm.notarizationList.length > 0) {
        var initialValue = 0;
        vm.fee = vm.notarizationList.reduce(function (accumulator, currentValue) {
          return accumulator + Number(currentValue.totalFee.toString().replace(/\./g, ''));
        }, initialValue);

        if (!vm.onlyView) {
          for (var key in vm.notarizationList) {
            vm.notarizationList[key].totalFee = vm.convertCurrency(vm.notarizationList[key].totalFee);
          }
        }
      }
    }).catch(function () {
      vm.loading = false;
    });
  },
  computed: {
    originality: function originality() {
      var vm = this;
      return vm.getOriginality();
    }
  },
  mounted: function mounted() {
    var vm = this;
  },
  watch: {
    '$route': function $route(newRoute, oldRoute) {
      var vm = this;
      var currentQuery = newRoute.query;
      var currentQueryOld = oldRoute.query;
    },
    notarizationList: function notarizationList() {
      var vm = this;

      for (var key in vm.notarizationList) {
        vm.doChangeSync[key] = '';
      }
    }
  },
  methods: {
    initData: function initData(data) {
      var vm = this;
    },
    showAddNotarization: function showAddNotarization() {
      var vm = this;
      vm.dialog_notarization = true;
      vm.fileName = '';
      vm.totalRecord = 1;
      vm.totalPage = 1;
      vm.$refs.formGuide.resetValidation();
    },
    addNotarization: function addNotarization() {
      var vm = this;
      var item = {
        fileName: '',
        totalRecord: 1,
        totalPage: 1,
        totalFee: vm.feeTotal(1, 1).toLocaleString('it-IT'),
        dossierId: vm.dossierId
      };
      vm.loading = true;
      vm.$store.dispatch('createNotarization', item).then(function (result) {
        vm.loading = false;
        result.totalFee = Number(result.totalFee.toString().replace(/\./g, '')).toLocaleString('it-IT');
        vm.notarizationList.push(result);
        var initialValue = 0;
        vm.fee = vm.notarizationList.reduce(function (accumulator, currentValue) {
          return accumulator + Number(currentValue.totalFee.toString().replace(/\./g, ''));
        }, initialValue);
      }).catch(function () {
        vm.loading = false;
      });
    },
    removeNotarization: function removeNotarization(item, index) {
      var vm = this;
      console.log('itemRemove', item);
      var x = confirm('Bạn có chắc chắn xóa tài liệu này?');

      if (x) {
        vm.$store.dispatch('deleteNotarization', item).then(function (result) {
          vm.notarizationList.splice(index, 1);
          var initialValue = 0;
          vm.fee = vm.notarizationList.reduce(function (accumulator, currentValue) {
            return accumulator + Number(currentValue.totalFee.toString().replace(/\./g, ''));
          }, initialValue);
        }).catch(function () {});
      }
    },
    changeDataItem: function changeDataItem(item, index) {
      var vm = this;
      vm.notarizationList[index].totalFee = Number(vm.feeTotal(item.totalRecord, item.totalPage)).toLocaleString('it-IT');
      var initialValue = 0;
      vm.fee = vm.notarizationList.reduce(function (accumulator, currentValue) {
        return accumulator + Number(currentValue.totalFee.toString().replace(/\./g, ''));
      }, initialValue);

      function changeData() {
        vm.doChangeSync[index] = setTimeout(function () {
          vm.$store.dispatch('updateNotarization', item).then(function (result) {}).catch(function () {});
        }, 1500);
      }

      clearTimeout(vm.doChangeSync[index]);
      changeData();
    },
    changeFeeItem: function changeFeeItem(item, index) {
      var vm = this;
      vm.notarizationList[index].totalFee = Number(vm.notarizationList[index].totalFee.toString().replace(/\./g, '')).toLocaleString('it-IT');
      var initialValue = 0;
      vm.fee = vm.notarizationList.reduce(function (accumulator, currentValue) {
        return accumulator + Number(currentValue.totalFee.toString().replace(/\./g, ''));
      }, initialValue);

      function changeData() {
        vm.doChangeSync[index] = setTimeout(function () {
          vm.$store.dispatch('updateNotarization', item).then(function (result) {}).catch(function () {});
        }, 1500);
      }

      clearTimeout(vm.doChangeSync[index]);
      changeData();
    },
    dataExport: function dataExport() {
      var vm = this;

      for (var key in vm.notarizationList) {
        vm.notarizationList[key].totalRecord = Number(vm.notarizationList[key].totalRecord);
        vm.notarizationList[key].totalPage = Number(vm.notarizationList[key].totalPage);
        vm.notarizationList[key].totalFee = Number(vm.notarizationList[key].totalFee.toString().replace(/\./g, ''));
      }

      var dataExport = {
        notarizationList: vm.notarizationList,
        feeTotal: vm.fee
      };
      console.log('dataExport', dataExport);
      return dataExport;
    },
    cancelAdd: function cancelAdd() {
      var vm = this;
    },
    feeTotal: function feeTotal(copy, page) {
      var vm = this;
      var copies = isNaN(Number(copy)) ? 0 : Number(copy);
      var pages = isNaN(Number(page)) ? 0 : Number(page);
      var mod = pages - 2;

      if (mod > 0) {
        var pay = copies * (2 * 2000 + mod * 1000);
        return pay < 200000 ? pay : 200000;
      } else {
        return copies * pages * 2000;
      }
    },
    convertCurrency: function convertCurrency(value) {
      if (value) {
        var moneyCur = (value / 1).toFixed(0).replace('.', ',');
        return moneyCur.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.');
      }

      return '';
    }
  },
  filters: {
    currency: function currency(value) {
      if (value) {
        var moneyCur = (value / 1).toFixed(0).replace('.', ',');
        return moneyCur.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.');
      }

      return '';
    }
  }
};