    paggingData: function paggingData(config) {
      var vm = this;
      vm.limitRecord = config.numberPerPage ? config.numberPerPage : 15;
      var current = vm.$router.history.current;
      var newQuery = current.query;
      var queryString = '?';
      newQuery['page'] = '';

      for (var key in newQuery) {
