    keywordEventChange: function keywordEventChange(data) {
      var vm = this;
      vm.selectMultiplePage = [];
      console.log('keywordEventChange', data);
      vm.advObjectSearch = {};

      for (var key in data) {
        if (typeof data[key] === 'string' && data[key] !== null && data[key] !== undefined && data[key] !== 'undefined') {
          if (!data[key].startsWith('keyword:') && !data[key].startsWith('year_month:') && !data[key].startsWith('top:') && !data[key].startsWith('status:') && !data[key].startsWith('substatus:') && !data[key].startsWith('agency:') && !data[key].startsWith('service:') && !data[key].startsWith('domain:') && !data[key].startsWith('register:')) {
            vm.advObjectSearch['keyword'] = data[key];
