export default {
  data: function data() {
    return {
      loading: true,
      domains: []
    };
  },
  created: function created() {
    var vm = this;
    vm.$nextTick(function () {
      vm.getDomains();
    });
  },
  methods: {
    getDomains: function getDomains() {
      var vm = this;
      var filter = {};
      vm.$store.dispatch('getDomains', filter).then(function (res) {
        console.log(res);
        vm.domains = res;
      });
    },
    redirectDVC: function redirectDVC(item) {
      var vm = this;
      var newQuery = {};
      var queryString = '?';
      newQuery['domain'] = item.domainCode;

      for (var key in newQuery) {
        if (newQuery[key] !== '' && newQuery[key] !== 'undefined' && newQuery[key] !== undefined && newQuery[key] !== null && newQuery[key] !== 'null') {
          queryString += key + '=' + newQuery[key] + '&';
        }
      }

      vm.$router.push({
        path: '/add-dvc/0' + queryString,
        query: {
          renew: Math.floor(Math.random() * (100 - 1 + 1)) + 1
        }
      });
    }
  }
};