import thongtinchunghoso from './form_xu_ly/ThongTinChungHoSo.vue';
import ThanhPhanHoSo from './TiepNhan/TiepNhanHoSo_ThanhPhanHoSoNew.vue';
export default {
  components: {
    'thongtinchunghoso': thongtinchunghoso,
    'thanh-phan-ho-so': ThanhPhanHoSo
  },
  props: {
    listLienThong: {
      type: Array,
      default: function _default() {
        return [];
      }
    },
    dossierDetail: {
      type: Object,
      default: function _default() {}
    }
  },
  data: function data() {
    return {
      loading: false,
      inputTypes: [1, 3],
      outputTypes: [2],
      detailView: false,
      thongTinLienThong: {},
      hoSoLienThongItems: [],
      currentHoSoLienThong: '',
      lienthongPage: 1,
      headers: [{
        text: 'STT',
        align: 'center',
        sortable: false
      }, {
        text: 'Tên hồ sơ',
        align: 'center',
        sortable: false
      }, {
        text: 'Mã hồ sơ',
        align: 'center',
        sortable: false
      }, {
        text: 'Cơ quan liên thông',
        align: 'center',
        sortable: false
      }, {
        text: 'Ngày gửi liên thông',
        align: 'center',
        sortable: false
      }, {
        text: 'Trạng thái',
        align: 'center',
        sortable: false
      }]
    };
  },
  computed: {},
  created: function created() {
    var vm = this;
    vm.$nextTick(function () {});
  },
  methods: {
    getDossierLogs: function getDossierLogs(dossier) {
      var vm = this;
      var data = {
        dossierId: dossier.dossierId,
        type: ''
      };
      vm.$store.dispatch('loadDossierLogs', data).then(function (result) {
        if (result.length > 0) {
          dossier.dossierLog = result;
        }
      }).catch(function (reject) {
        console.log(reject);
      });
    },
    viewDetail: function viewDetail(item) {
      var vm = this;
      console.log('item', item);
      vm.detailView = true;
      vm.currentHoSoLienThong = item;
      setTimeout(function () {
        if (vm.$refs.thanhphanhoso1) {
          vm.$refs.thanhphanhoso1.initData(item);
        }

        if (vm.$refs.thanhphanhoso2) {
          vm.$refs.thanhphanhoso2.initData(item);
        }
      }, 300);
    },
    goBack: function goBack() {
      window.history.back();
    }
  },
  filters: {
    dateTimeView: function dateTimeView(arg) {
      if (arg) {
        var value = new Date(arg);
        return "".concat(value.getDate().toString().padStart(2, '0'), "/").concat((value.getMonth() + 1).toString().padStart(2, '0'), "/").concat(value.getFullYear(), "  ").concat(value.getHours().toString().padStart(2, '0'), ":").concat(value.getMinutes().toString().padStart(2, '0'));
      } else {
        return '';
      }
    }
  }
};