export default {
  data: function data() {
    return {
      thongTinChiTietHoSo: {}
    };
  },
  computed: {
    loading: function loading() {
      return this.$store.getters.loading;
    }
  },
  created: function created() {},
  methods: {
    initData: function initData(result) {
      this.thongTinChiTietHoSo = result;
    },
    durationText: function durationText(durationUnit, durationCount) {
      var durationText;

      if (durationUnit === 1 && durationCount > 8) {
        var day = Math.floor(durationCount / 8) + ' ngày';
        var hours;

        if (durationCount % 8 !== 0) {
          hours = (durationCount / 8 - Math.floor(durationCount / 8)) * 8 + ' giờ';
        } else {
          hours = '';
        }

        durationText = "".concat(day, " ").concat(hours);
      } else if (durationUnit === 0) {
        durationText = durationCount + ' ngày';
      } else if (durationUnit === 1 && durationCount <= 8) {
        durationText = durationCount + ' giờ';
      }

      return durationText;
    }
  },
  filters: {
    dateTimeView: function dateTimeView(arg) {
      if (arg) {
        var value = new Date(arg);
        return "".concat(value.getDate().toString().padStart(2, '0'), "/").concat((value.getMonth() + 1).toString().padStart(2, '0'), "/").concat(value.getFullYear(), "  ").concat(value.getHours().toString().padStart(2, '0'), ":").concat(value.getMinutes().toString().padStart(2, '0'));
      } else {
        return '';
      }
    }
  }
};