// import $ from 'jquery'
import toastr from 'toastr';
toastr.options = {
  'closeButton': true,
  'timeOut': '2000'
};
import toggleButton from '../toggleButton.vue';
export default {
  components: {
    'toggle-button': toggleButton
  },
  props: {
    assign_items: {
      type: Array,
      default: function _default() {
        return [];
      }
    },
    data_uyquyen: {
      type: Array,
      default: function _default() {
        return [];
      }
    },
    type: {
      type: Number,
      default: function _default() {
        return 1;
      }
    },
    configNote: {
      type: Object,
      default: function _default() {}
    },
    detailDossier: {
      type: Object,
      default: function _default() {}
    }
  },
  model: {
    prop: 'assign_items'
  },
  data: function data() {
    return {
      data_phancong: [],
      assignedtype_items: {
        '2': [{
          text: 'Không có phân công',
          value: 0
        }, {
          text: 'Thực hiện chính',
          value: 1
        }, {
          text: 'Thực hiện phối hợp',
          value: 2
        }],
        '3': [{
          text: 'Không có phân công',
          value: 0
        }, {
          text: 'Thực hiện chính',
          value: 1
        }, {
          text: 'Thực hiện phối hợp',
          value: 2
        }, {
          text: 'Theo dõi',
          value: 3
        }]
      },
      title_asign: {
        '1': 'PHÂN CÔNG NGƯỜI THỰC HIỆN',
        '2': 'PHÂN CÔNG THỰC HIỆN, PHỐI HỢP',
        '3': 'PHÂN CÔNG THỰC HIỆN, PHỐI HỢP VÀ THEO DÕI'
      },
      presenterAddGroup: true,
      assignValidate: true
    };
  },
  created: function created() {
    var vm = this;
    setTimeout(function () {
      window.$('.btn-tgl').addClass('btn-hidden');
    }, 200);
  },
  mounted: function mounted() {
    this.data_phancong = this.assign_items;
  },
  methods: {
    changeAssigned: function changeAssigned(event, index) {
      var vm = this;

      if (vm.type !== 0) {
        if (event === true) {
          vm.assign_items[index].assigned = 1;
        } else {
          vm.assign_items[index].assigned = 0;
        }
      }
    },
    changeDelegacy: function changeDelegacy(event, index) {
      var vm = this;

      if (vm.type === 3 || vm.type === 4) {
        if (event === true) {
          vm.data_uyquyen[index].assigned = 1;
        } else {
          vm.data_uyquyen[index].assigned = 0;
        }
      }
    },
    checkAsign: function checkAsign(event, index) {
      var vm = this;

      if (event) {
        vm.assign_items[index].assigned = 1;
        window.$("#btn-".concat(index)).removeClass('btn-hidden');
      } else {
        vm.assign_items[index].assigned = 0;
        window.$("#btn-".concat(index)).addClass('btn-hidden');
      } // console.log('vm.assign_items', vm.assign_items)

    },
    changeTypeAssign: function changeTypeAssign(event, index) {
      var vm = this;

      if (event.value) {
        vm.assign_items[index].assigned = 1;
        window.$("#btn-".concat(index)).removeClass('btn-hidden');
      } else {
        vm.assign_items[index].assigned = 2;
        window.$("#btn-".concat(index)).addClass('btn-hidden');
      } // console.log('vm.assign_items', vm.assign_items)

    },
    doReAsign: function doReAsign() {
      var vm = this;
      var filter = {
        'dossierId': vm.detailDossier.dossierId,
        'users': vm.data_uyquyen
      };
      vm.$store.dispatch('postDossierUserAsign', filter).then(function (result) {
        toastr.success('Yêu cầu của bạn thực hiện thành công');
      }).catch(function (error) {});
    },
    doExport: function doExport() {
      var vm = this;
      var assign = vm.assign_items.filter(function (item) {
        return Number(item.assigned) > 0;
      });

      if (assign.length === 0) {
        vm.assignValidate = false;
        return vm.assignValidate;
      } else {
        vm.assignValidate = true;
        return vm.assignValidate;
      }
    },
    getDataDelegacy: function getDataDelegacy() {
      var vm = this;
      return vm.data_uyquyen;
    }
  }
};