export default {
  props: {
    postalService: {
      type: Object,
      default: function _default() {}
    }
  },
  data: function data() {
    return {
      postName: ''
    };
  },
  computed: {},
  created: function created() {
    var vm = this;
    vm.$nextTick(function () {
      var vm = this;
      vm.$store.dispatch('getVNPOSTcode').then(function (result) {
        var postItem = result.filter(function (item) {
          return Number(item.itemCode) === Number(vm.postalService.postalCityCode);
        });

        if (postItem.length > 0) {
          vm.postName = postItem[0].itemName;
        }
      });
    });
  },
  methods: {}
};