import { VMoney } from 'v-money';
export default {
  props: {
    payments: {
      type: Object,
      default: function _default() {}
    },
    viaPortal: {
      type: Number,
      default: function _default() {
        return 0;
      }
    },
    detailDossier: {
      type: Object,
      default: function _default() {}
    }
  },
  model: {
    prop: 'payments'
  },
  data: function data() {
    return {
      data_payment: {},
      money: {
        decimal: '',
        thousands: '.',
        prefix: '',
        suffix: '',
        precision: 0,
        masked: false
      },
      totalFee: 0,
      feeTamThu: 0,
      feeTong: 0,
      checkPaid: true,
      activeEdit: true,
      paymentFile: '',
      dialogPDF: false,
      dialogPDFLoading: true
    };
  },
  directives: {
    money: VMoney
  },
  created: function created() {
    var vm = this;
    vm.data_payment = vm.payments;

    if (vm.payments) {
      setTimeout(function () {
        var feeAmount = typeof vm.payments.feeAmount === 'number' ? Number(vm.payments.feeAmount) : Number(vm.payments.feeAmount.toString().replace(/\./g, '')); // let shipAmount = vm.payments.shipAmount ? Number(vm.payments.shipAmount.toString().replace(/\./g, '')) : 0

        if (vm.payments.requestPayment === 1) {
          // let advanceAmount = vm.payments.advanceAmount ? Number(vm.payments.advanceAmount.toString().replace(/\./g, '')) : 0
          var serviceAmount = typeof vm.payments.serviceAmount === 'number' ? Number(vm.payments.serviceAmount) : Number(vm.payments.serviceAmount.toString().replace(/\./g, ''));
          vm.feeTong = feeAmount + serviceAmount;
          vm.totalFee = feeAmount + serviceAmount;
        } else if (vm.payments.requestPayment === 2) {
          var _serviceAmount = vm.payments.serviceAmount ? Number(vm.payments.serviceAmount.toString().replace(/\./g, '')) : 0;

          var shipAmount = vm.payments.shipAmount ? Number(vm.payments.shipAmount.toString().replace(/\./g, '')) : 0;

          if ((vm.viaPortal === 2 || vm.viaPortal === '2') && shipAmount !== 0) {
            vm.totalFee = feeAmount + _serviceAmount + shipAmount;
          } else {
            vm.totalFee = feeAmount + _serviceAmount;
          }
        } else {
          var advanceAmount = vm.payments.advanceAmount ? Number(vm.payments.advanceAmount.toString().replace(/\./g, '')) : 0;

          var _serviceAmount2 = vm.payments.serviceAmount ? Number(vm.payments.serviceAmount.toString().replace(/\./g, '')) : 0;

          var _shipAmount = vm.payments.shipAmount ? Number(vm.payments.shipAmount.toString().replace(/\./g, '')) : 0;

          if ((vm.viaPortal === 2 || vm.viaPortal === '2') && _shipAmount !== 0) {
            vm.feeTong = feeAmount + _serviceAmount2 + _shipAmount;
            vm.totalFee = feeAmount + _serviceAmount2 + _shipAmount - advanceAmount;
          } else {
            vm.feeTong = feeAmount + _serviceAmount2;
            vm.totalFee = feeAmount + _serviceAmount2 - advanceAmount;
          }

          if (vm.payments.requestPayment === 5) {
            var filter = vm.detailDossier;
            vm.$store.dispatch('getPaymentFiles', filter).then(function (result) {
              vm.paymentFile = result;
            });
          }
        }

        if (vm.totalFee < 0) {
          vm.totalFee = 0;
        }
      }, 300);
    }
  },
  computed: {
    paymentFileName: function paymentFileName() {
      return this.$store.getters.getPaymentFileName;
    },
    originality: function originality() {
      var vm = this;
      return vm.getOriginality();
    }
  },
  watch: {
    viaPortal: function viaPortal(val) {
      console.log('viaPortal', val);
      this.changeFee();
    },
    detailDossier: function detailDossier(val) {
      var vm = this;

      if (vm.payments.requestPayment === 5) {
        var filter = val;
        console.log('run get paymentfile', filter);
        vm.$store.dispatch('getPaymentFiles', filter).then(function (result) {
          vm.paymentFile = result;
        });
      }
    }
  },
  methods: {
    changeFee: function changeFee() {
      var vm = this; // console.log('changeFee')

      var val = vm.data_payment;
      var feeAmount = val.feeAmount ? Number(val.feeAmount.toString().replace(/\./g, '')) : 0;

      if (val.requestPayment === 1) {
        // let advanceAmount = Number(val.advanceAmount.toString().replace(/\./g, ''))
        var serviceAmount = vm.payments.serviceAmount ? Number(vm.payments.serviceAmount.toString().replace(/\./g, '')) : 0;
        vm.feeTong = feeAmount + serviceAmount;
        vm.totalFee = feeAmount + serviceAmount; // console.log('changeFee', vm.feeTong, vm.totalFee)
      } else if (val.requestPayment === 2) {
        var _serviceAmount3 = vm.payments.serviceAmount ? Number(vm.payments.serviceAmount.toString().replace(/\./g, '')) : 0;

        var shipAmount = vm.payments.shipAmount ? Number(vm.payments.shipAmount.toString().replace(/\./g, '')) : 0;

        if ((vm.viaPortal === 2 || vm.viaPortal === '2') && shipAmount !== 0) {
          vm.totalFee = feeAmount + _serviceAmount3 + shipAmount;
        } else {
          vm.totalFee = feeAmount + _serviceAmount3;
        }
      } else {
        var advanceAmount = Number(val.advanceAmount.toString().replace(/\./g, ''));

        var _serviceAmount4 = Number(val.serviceAmount.toString().replace(/\./g, ''));

        var _shipAmount2 = Number(val.shipAmount.toString().replace(/\./g, ''));

        if ((vm.viaPortal === 2 || vm.viaPortal === '2') && _shipAmount2 !== 0) {
          vm.feeTong = feeAmount + _serviceAmount4 + _shipAmount2;
          vm.totalFee = feeAmount + _serviceAmount4 + _shipAmount2 - advanceAmount;
        } else {
          vm.feeTong = feeAmount + _serviceAmount4;
          vm.totalFee = feeAmount + _serviceAmount4 - advanceAmount;
        }
      }

      if (vm.totalFee < 0) {
        vm.totalFee = 0;
      }
    },
    viewFile: function viewFile() {
      var vm = this;
      vm.dialogPDFLoading = true;
      vm.dialogPDF = true;
      var filter = {
        dossierId: vm.detailDossier.dossierId,
        referenceUid: vm.detailDossier.referenceUid
      };
      vm.$store.dispatch('viewPaymentFile', filter).then(function (result) {
        vm.dialogPDFLoading = false;
        document.getElementById('dialogPreview').src = result;
      });
    },
    downloadPaymentFile: function downloadPaymentFile(item) {
      var vm = this; // test local
      // let url = 'http://127.0.0.1:8081/api/dossiers/' + vm.dossierId + '/payment/confirmfile'

      var url = '/o/rest/v2/dossiers/' + vm.detailDossier.dossierId + '/payments/' + vm.detailDossier.referenceUid + '/confirmfile';
      window.open(url);
    },
    currency: function currency(value) {
      if (value) {
        var moneyCur = (value / 1).toFixed(0).replace('.', ',');
        return moneyCur.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.');
      }

      return '';
    },
    goBack: function goBack() {
      window.history.back();
    }
  },
  filters: {}
};