import Vue from 'vue';
import $ from 'jquery';
import VueDraggable from 'vue-draggable';
Vue.use(VueDraggable);
export default {
  props: ['index'],
  components: {},
  data: function data() {
    return {
      options: {
        dropzoneSelector: 'ul.captcha-wrap'
      },
      arrCaptcha: [],
      captcha: '',
      currentlyDragging: null,
      loggedEvent: '',
      images: [{
        name: 'Image 1',
        src: 'http://placehold.it/100/000000/ffffff'
      }, {
        name: 'Image 2',
        src: 'http://placehold.it/100/C93742/ffffff'
      }, {
        name: 'Image 3',
        src: 'http://placehold.it/100/6894D1/ffffff'
      }]
    };
  },
  computed: {
    loading: function loading() {
      return this.$store.getters.loading;
    }
  },
  created: function created() {
    var vm = this;
    vm.makeRandomString();
    vm.shuffleArrCaptcha(vm.arrCaptcha);
  },
  watch: {},
  methods: {
    makeRandomString: function makeRandomString() {
      var vm = this;
      var text = '';
      var possible = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';

      for (var i = 0; i < 4; i++) {
        text += possible.charAt(Math.floor(Math.random() * possible.length));
      }

      if (text) {
        vm.arrCaptcha = vm.shuffleArrCaptcha(text.split(''));
      }

      vm.captcha = text;
    },
    shuffleArrCaptcha: function shuffleArrCaptcha(arra1) {
      var ctr = arra1.length;
      var temp = null;
      var index = 0;

      while (ctr > 0) {
        index = Math.floor(Math.random() * ctr);
        ctr--;
        temp = arra1[ctr];
        arra1[ctr] = arra1[index];
        arra1[index] = temp;
      }

      return arra1;
    },
    checkValidCatcha: function checkValidCatcha() {
      var vm = this;
      var str = '';
      var items = $('#captcha').children();
      console.log(items);

      for (var i = 0; i < items.length; i++) {
        str += items[i].innerHTML;
      }

      if (vm.captcha === str) {
        return true;
      } else {
        return false;
      }
    }
  },
  filters: {
    dateTimeView: function dateTimeView(arg) {
      if (arg) {
        var value = new Date(arg);
        return "".concat(value.getDate().toString().padStart(2, '0'), "/").concat((value.getMonth() + 1).toString().padStart(2, '0'), "/").concat(value.getFullYear());
      } else {
        return '';
      }
    }
  }
};