// import $ from 'jquery'
import toggleButton from '../toggleButton.vue';
export default {
  components: {
    'toggle-button': toggleButton
  },
  props: {
    reAsignUsers: {
      type: Array,
      default: function _default() {
        return [];
      }
    },
    type: {
      type: Number,
      default: function _default() {
        return 1;
      }
    },
    configNote: {
      type: Object,
      default: function _default() {}
    }
  },
  model: {
    prop: 'reAsignUsers'
  },
  data: function data() {
    return {
      data_phancong: [],
      assignedtype_items: {
        '2': [{
          text: 'Không có phân công',
          value: 0
        }, {
          text: 'Thực hiện chính',
          value: 1
        }, {
          text: 'Thực hiện phối hợp',
          value: 2
        }],
        '3': [{
          text: 'Không có phân công',
          value: 0
        }, {
          text: 'Thực hiện chính',
          value: 1
        }, {
          text: 'Thực hiện phối hợp',
          value: 2
        }, {
          text: 'Theo dõi',
          value: 3
        }]
      },
      title_asign: {
        '1': 'PHÂN CÔNG NGƯỜI THỰC HIỆN',
        '2': 'PHÂN CÔNG THỰC HIỆN, PHỐI HỢP',
        '3': 'PHÂN CÔNG THỰC HIỆN, PHỐI HỢP VÀ THEO DÕI'
      },
      presenterAddGroup: true,
      assignValidate: true
    };
  },
  created: function created() {
    var vm = this;
    setTimeout(function () {
      window.$('.btn-tgl').addClass('btn-hidden');
    }, 200);
  },
  mounted: function mounted() {
    this.data_phancong = this.reAsignUsers;
  },
  watch: {
    reAsignUsers: function reAsignUsers(val) {
      this.data_phancong = val;
    }
  },
  methods: {
    changeAssigned: function changeAssigned(event, index) {
      var vm = this;

      if (vm.type === 1) {
        if (event === true) {
          vm.reAsignUsers[index].assigned = 1;
        } else {
          vm.reAsignUsers[index].assigned = 0;
        }
      }
    },
    checkAsign: function checkAsign(event, index) {
      var vm = this;

      if (event) {
        vm.reAsignUsers[index].assigned = 1;
        window.$("#btn-".concat(index)).removeClass('btn-hidden');
      } else {
        vm.reAsignUsers[index].assigned = 0;
        window.$("#btn-".concat(index)).addClass('btn-hidden');
      } // console.log('vm.reAsignUsers', vm.reAsignUsers)

    },
    changeTypeAssign: function changeTypeAssign(event, index) {
      var vm = this;

      if (event.value) {
        vm.reAsignUsers[index].assigned = 1;
        window.$("#btn-".concat(index)).removeClass('btn-hidden');
      } else {
        vm.reAsignUsers[index].assigned = 2;
        window.$("#btn-".concat(index)).addClass('btn-hidden');
      } // console.log('vm.reAsignUsers', vm.reAsignUsers)

    },
    doExport: function doExport() {
      var vm = this;
      var assign = vm.reAsignUsers.filter(function (item) {
        return Number(item.assigned) > 0;
      });

      if (assign.length === 0) {
        vm.assignValidate = false;
        return vm.assignValidate;
      } else {
        vm.assignValidate = true;
        return vm.assignValidate;
      }
    }
  }
};