var COMPONENT_NAME = 'TinyPagination';
import { isMobile } from 'mobile-device-detect';
export default {
  name: COMPONENT_NAME,
  props: {
    total: {
      type: Number,
      required: true
    },
    nameRecord: {
      type: String,
      default: 'hồ sơ'
    },
    page: {
      type: Number,
      default: 1
    },
    numberPerPage: {
      type: Number,
      default: 15
    },
    lang: {
      type: String,
      default: 'en'
    },
    customClass: {
      type: String
    },
    limits: {
      type: Array,
      default: function _default() {
        return [15, 30, 50, 100];
      }
    },
    showLimit: {
      type: Boolean,
      default: false
    }
  },
  data: function data() {
    return {
      version: '0.2.1',
      currentPage: 1,
      translations: {
        en: {
          prev: 'Previous',
          title: 'Page',
          next: 'Next'
        }
      },
      availableLanguages: ['en']
    };
  },
  created: function created() {
    this.currentPage = this.page; // console.log('total, numberPerPage', this.total, this.numberPerPage)
  },
  watch: {
    page: function page(val) {
      this.currentPage = parseInt(val);
    }
  },
  computed: {
    translation: function translation() {
      return this.availableLanguages.includes(this.lang) ? this.translations[this.lang] : this.translations['en'];
    },
    totalPages: function totalPages() {
      return Math.ceil(this.total / this.numberPerPage);
    },
    totalPagesData: function totalPagesData() {
      var totalDatas = [];
      var data = {};

      for (var index = 1; index <= this.totalPages; ++index) {
        data = {
          value: index,
          text: 'Trang ' + index
        };
        totalDatas.push(data);
      }

      return totalDatas;
    },
    titlePage: function titlePage() {
      return "".concat(this.currentPage);
    },
    classFirstPage: function classFirstPage() {
      return {
        'c-not-allowed pagination__navigation--disabled': this.currentPage === 1
      };
    },
    classLastPage: function classLastPage() {
      return {
        'c-not-allowed pagination__navigation--disabled': this.currentPage === this.totalPages
      };
    },
    viewMobile: function viewMobile() {
      return isMobile;
    }
  },
  methods: {
    nextPage: function nextPage() {
      if (this.currentPage !== this.totalPages) {
        this.currentPage += 1;
        this.$emit('tiny:change-page', {
          page: this.currentPage,
          numberPerPage: this.numberPerPage
        });
      }
    },
    lastPage: function lastPage() {
      if (this.currentPage > 1) {
        this.currentPage -= 1;
        this.$emit('tiny:change-page', {
          page: this.currentPage,
          numberPerPage: this.numberPerPage
        });
      }
    },
    nextPageLast: function nextPageLast() {
      this.currentPage = this.totalPages;
      this.$emit('tiny:change-page', {
        page: this.totalPages,
        numberPerPage: this.numberPerPage
      });
    },
    lastPageLast: function lastPageLast() {
      this.currentPage = 1;
      this.$emit('tiny:change-page', {
        page: 1,
        numberPerPage: this.numberPerPage
      });
    },
    goToPage: function goToPage() {
      this.$emit('tiny:change-page', {
        page: this.currentPage,
        numberPerPage: this.numberPerPage
      });
    },
    onLimitChange: function onLimitChange() {
      this.currentPage = 1;
      this.$emit('tiny:change-page', {
        page: 1,
        numberPerPage: this.numberPerPage
      });
    }
  }
};