var datePicker = window.VueCtkDateTimePicker ? window.VueCtkDateTimePicker.default : window['vue-ctk-date-time-picker'];
export default {
  components: {
    'vue-ctk-date-time-picker': datePicker
  },
  data: function data() {
    return {
      minDate: null,
      maxDate: null,
      editable: false,
      dueDateInput: null,
      dataPostDossier: {
        serviceCode: '',
        govAgencyCode: '',
        processOptionId: ''
      },
      thongTinChungHoSo: {
        serviceName: '',
        dossierTemplateName: '',
        serviceConfig: '',
        serviceOption: '',
        dossierNo: '',
        receiveDate: '',
        dueDate: '',
        durationDate: '',
        dossierId: '',
        dossierIdCTN: '',
        dossierStatus: '',
        dossierStatusText: '',
        durationUnit: '',
        durationCount: ''
      }
    };
  },
  created: function created() {
    var vm = this;
    vm.$nextTick(function () {
      vm.minDate = vm.getCurentDateTime('date');
    });
  },
  computed: {
    loading: function loading() {
      return this.$store.getters.loading;
    },
    isDetail: function isDetail() {
      return this.$store.getters.isDetail;
    }
  },
  watch: {
    dueDateInput: function dueDateInput(val) {
      this.thongTinChungHoSo['dueDate'] = this.getDuedateF(val);
    }
  },
  methods: {
    initData: function initData(data) {
      var vm = this;
      var thongTinChungHoSoTemp = {
        serviceName: data.serviceName,
        dossierTemplateName: data.dossierTemplateName,
        dossierNo: data.dossierNo ? data.dossierNo : '',
        dueDate: null,
        receiveDate: data.receivingDate ? vm.dateTimeView(data.receivingDate) : data.receiveDate,
        durationDate: data.durationDate,
        dossierId: data.dossierId,
        dossierIdCTN: data.dossierIdCTN,
        dossierStatus: data.dossierStatus,
        dossierStatusText: data.dossierStatusText,
        durationUnit: data.durationUnit,
        durationCount: data.durationCount
      };

      if (data.receivingDuedate && data.editable) {
        thongTinChungHoSoTemp.dueDate = data.receivingDuedate;
      } else if (data.receivingDuedate && !data.editable) {
        thongTinChungHoSoTemp.dueDate = data.receivingDuedate ? vm.dateTimeView(data.receivingDuedate) : data.dueDate;
      } else {
        thongTinChungHoSoTemp.dueDate = data.dueDate;
      }

      vm.thongTinChungHoSo = thongTinChungHoSoTemp;
      vm.editable = data.editable;
      vm.thongTinChungHoSo['editable'] = vm.editable;

      if (vm.editable) {
        vm.dueDateInput = vm.thongTinChungHoSo.dueDate ? vm.formatDateInput(new Date(Number(vm.thongTinChungHoSo.dueDate))) : null;
        vm.maxDate = vm.thongTinChungHoSo.dueDate ? vm.formatDateInput(new Date(Number(vm.thongTinChungHoSo.dueDate))) : null;
      }

      vm.minDate = vm.getCurentDateTime('date');
    },
    getthongtinchunghoso: function getthongtinchunghoso() {
      return this.thongTinChungHoSo;
    },
    getCurentDateTime: function getCurentDateTime(type) {
      var date = new Date();

      if (type === 'datetime') {
        return "".concat(date.getDate().toString().padStart(2, '0'), "/").concat((date.getMonth() + 1).toString().padStart(2, '0'), "/").concat(date.getFullYear(), " | ").concat(date.getHours().toString().padStart(2, '0'), ":").concat(date.getMinutes().toString().padStart(2, '0'));
      } else if (type === 'date') {
        return "".concat(date.getFullYear(), "-").concat((date.getMonth() + 1).toString().padStart(2, '0'), "-").concat(date.getDate());
      }
    },
    changeDate: function changeDate() {
      this.thongTinChungHoSo['dueDate'] = this.getDuedate(); // console.log('dueDate', this.thongTinChungHoSo.dueDate)
    },
    changeDossierNo: function changeDossierNo(val) {
      this.thongTinChungHoSo['dossierNo'] = val;
    },
    getDuedate: function getDuedate() {
      var vm = this;
      var date = this.dueDateInput ? new Date(this.dueDateInput).getTime() : '';
      return date;
    },
    getDuedateF: function getDuedateF(val) {
      var vm = this;
      var date = new Date(val).getTime();
      return date;
    },
    durationText: function durationText(durationUnit, durationCount) {
      var durationText;

      if (durationUnit === 1 && durationCount > 8) {
        var day = Math.floor(durationCount / 8) + ' ngày';
        var hours;

        if (durationCount % 8 !== 0) {
          hours = (durationCount / 8 - Math.floor(durationCount / 8)) * 8 + ' giờ';
        } else {
          hours = '';
        }

        durationText = "".concat(day, " ").concat(hours);
      } else if (durationUnit === 0) {
        durationText = durationCount + ' ngày';
      } else if (durationUnit === 1 && durationCount <= 8) {
        durationText = durationCount + ' giờ';
      }

      return durationText;
    },
    goBack: function goBack() {
      var vm = this;
      var currentParams = vm.$router.history.current.params;
      var currentQuery = vm.$router.history.current.query;
      vm.$router.push({
        path: '/danh-sach-ho-so/' + currentParams.index,
        query: currentQuery
      });
    },
    dateTimeView: function dateTimeView(arg) {
      if (arg) {
        var value = new Date(arg);
        return "".concat(value.getDate().toString().padStart(2, '0'), "/").concat((value.getMonth() + 1).toString().padStart(2, '0'), "/").concat(value.getFullYear(), " ").concat(value.getHours().toString().padStart(2, '0'), ":").concat(value.getMinutes().toString().padStart(2, '0'));
      }
    },
    formatDateInput: function formatDateInput(date) {
      return "".concat(date.getFullYear(), "-").concat((date.getMonth() + 1).toString().padStart(2, '0'), "-").concat(date.getDate().toString().padStart(2, '0'), " ").concat(date.getHours().toString().padStart(2, '0'), ":").concat(date.getMinutes().toString().padStart(2, '0'));
    },
    showDatePicker: function showDatePicker() {
      this.$refs.datepicker.showDatePicker();
    }
  },
  filters: {
    dateTimeView: function dateTimeView(arg) {
      if (arg) {
        var value = new Date(arg);
        return "".concat(value.getDate().toString().padStart(2, '0'), "/").concat((value.getMonth() + 1).toString().padStart(2, '0'), "/").concat(value.getFullYear(), " ").concat(value.getHours().toString().padStart(2, '0'), ":").concat(value.getMinutes().toString().padStart(2, '0'));
      } else {
        return '';
      }
    }
  }
};