export default {
  props: {
    user_note: {
      type: Number,
      required: true
    },
    configNote: {
      type: Object,
      required: false
    }
  },
  data: function data() {
    return {
      noteYkien: '',
      valid: false
    };
  },
  methods: {
    initData: function initData(data) {
      var vm = this;
      vm.$store.dispatch('getDetailDossier', data).then(function (resultDossier) {
        vm.thongTinChiTietHoSo = resultDossier;
      });
    },
    doExport: function doExport() {
      var vm = this;
      var exportData = {
        text: vm.noteYkien,
        valid: vm.$refs.userNoteForm.validate()
      };
      return exportData;
    }
  }
};