    saveMark: function saveMark() {
      var vm = this;

      if (vm.dossierTemplateItems) {
        vm.dossierTemplateItems.forEach(function (value, index) {
          if (value.partType === 1) {
            value['dossierId'] = vm.thongTinHoSo.dossierId;
            vm.$store.dispatch('postDossierMark', value);
          }
        });
      }
