export default {
  props: {
    id: {
      type: Number,
      default: function _default() {
        return 0;
      }
    },
    index: {
      type: Number,
      default: function _default() {
        return 0;
      }
    }
  },
  data: function data() {
    return {
      thongTinChungHoSo: {},
      dossierTemplateItems: [],
      dossierFilesItems: [],
      dossierTemplateSendItems: []
    };
  },
  created: function created() {
    var vm = this;
    vm.$nextTick(function () {
      vm.$store.dispatch('getDetailDossier', vm.id).then(function (resultDossier) {
        vm.thongTinChungHoSo = resultDossier;
        var arrTemp = [];
        arrTemp.push(vm.$store.dispatch('loadDossierTemplates', resultDossier));
        arrTemp.push(vm.$store.dispatch('loadDossierFiles', resultDossier.dossierId));
        Promise.all(arrTemp).then(function (results) {
          vm.dossierTemplateItems = results[0]['dossierParts'];
          vm.dossierFilesItems = results[1];
          vm.dossierTemplateSendItems = vm.filterDossierTemplateSend();
        }).catch(function (xhr) {});
      });
    });
  },
  computed: {
    loading: function loading() {
      return this.$store.getters.loading;
    }
  },
  methods: {
    initData: function initData(data) {
      var vm = this;
      vm.thongTinChungHoSo = data;
    },
    goBack: function goBack() {
      var vm = this; // let currentParams = vm.$router.history.current.params
      // let currentQuery = vm.$router.history.current.query
      // vm.$router.push({
      //   path: '/danh-sach-ho-so/' + currentParams.index,
      //   query: currentQuery
      // })

      vm.$router.push({
        path: '/danh-sach-ho-so/' + 0,
        query: {
          recount: Math.floor(Math.random() * (100 - 1 + 1)) + 1,
          renew: Math.floor(Math.random() * (100 - 1 + 1)) + 1,
          q: '/o/rest/v2/dossiers/todo?owner=true&order=true&step=110,130,510'
        }
      }); // window.history.back()
    },
    filterDossierTemplateSend: function filterDossierTemplateSend() {
      var vm = this;
      var arrTemp = [];

      if (vm.dossierTemplateItems.length > 0 && vm.dossierFilesItems.length > 0) {
        vm.dossierTemplateItems.forEach(function (template) {
          var indexTemp = vm.dossierFilesItems.findIndex(function (file) {
            return template.partNo === file.dossierPartNo;
          });

          if (indexTemp !== -1) {
            arrTemp.push(template);
          }
        });
        return arrTemp;
      } else {
        return [];
      }
    },
    back: function back() {// var vm = this
    },
    dateTimeView: function dateTimeView(arg) {
      if (arg) {
        var value = new Date(arg);
        return "".concat(value.getDate().toString().padStart(2, '0'), "/").concat((value.getMonth() + 1).toString().padStart(2, '0'), "/").concat(value.getFullYear(), " ").concat(value.getHours().toString().padStart(2, '0'), ":").concat(value.getMinutes().toString().padStart(2, '0'));
      }
    },
    formatDateInput: function formatDateInput(date) {
      return "".concat(date.getFullYear(), "-").concat((date.getMonth() + 1).toString().padStart(2, '0'), "-").concat(date.getDate().toString().padStart(2, '0'), " ").concat(date.getHours().toString().padStart(2, '0'), ":").concat(date.getMinutes().toString().padStart(2, '0'));
    }
  },
  filters: {
    dateTimeView: function dateTimeView(arg) {
      if (arg) {
        var value = new Date(arg);
        return "".concat(value.getDate().toString().padStart(2, '0'), "/").concat((value.getMonth() + 1).toString().padStart(2, '0'), "/").concat(value.getFullYear(), " ").concat(value.getHours().toString().padStart(2, '0'), ":").concat(value.getMinutes().toString().padStart(2, '0'));
      } else {
        return '';
      }
    }
  }
};